<template>
  <div class="container">
    <h3 class="grey-text text-darken-2">Pick a Product!</h3>
    <h5 class="grey-text text-darken-2">...and have a great shopping experience!</h5>
    <br>   
    <br>
    <p class="error" v-if="error">{{ error }}</p>
    <div class="items-container">
      <div class="row">
        <div class="item"
         v-for="(item, index) in items"
         v-bind:item="item"
         v-bind:index="index"
         v-bind:key="item._id"
        >      
          <div class="col s12 m8">
            <div class="card horizontal">
              <div class="card-image">
                <img :src="item.image">
              </div>
              <div class="card-stacked">
                <div class="card-content">
                  <h5>{{ item.name }}</h5>
                  <br>
                  <div class="orange-text text-darken-2">
                    <p>Price: {{ item.price }} EUR</p> 
                  </div>
                  <br>                 
                  <a class="light-blue btn-small" @click="addItem(item)">Add to basket</a>
                </div>                
              </div>
            </div>                         
          </div>         
        </div>
      </div>     
    </div>
  </div>
</template>

<script>
import StoreService from "../StoreService";
import BasketService from "../BasketService";

export default {
  name: "Store",
  data() {
    return {
      items: [],
      error: ""
    };
  },
  methods: {
     async addItem(item) {
      try {
        await BasketService.insertItem({
          _id: item._id,
          name: item.name,
          price: item.price,
          image: item.image
        });
      } catch (err) {
        this.error = err.message;
      }
    }
  },
  async created() {
    try {
      this.items = await StoreService.getItems();
    } catch (err) {
      this.error = err.message;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
div.container {
  max-width: 800px;
  margin: 0 auto;
}
p.error {
  border: 1px solid #ff5b5f;
  background-color: #ffc5c1;
  padding: 10px;
  margin-bottom: 15px;
}
span.new.badge.pointer:hover {
  cursor: pointer;
  background: #0277bd;
}
</style>
