import axios from "axios";

const url = "api/";

class StoreService {
  // Get Items
  static getItems() {
    return new Promise(async (resolve, reject) => {
      try {
        const res = await axios.get(url);
        resolve(res.data);
      } catch (err) {
        reject(err);
      }
    });
  }
}

export default StoreService;
