<template>
  <div class="container">
    <form 
      id="app"
      @submit="placeOrder"
      method="post"
      novalidate="true"
      >
    <h4 class="grey-text text-darken-2">Checkout</h4>
    <h5 class="grey-text text-darken-2"><strong>Total Price: {{ this.$route.params.price.toFixed(2) }} EUR</strong></h5> 
    <div class="row">
      <br>
      <h6 class="grey-text text-darken-2">Please fill in the data   </h6>
      <div class="input-field col s8">
        <input id="email" type="email" class="validate" v-model="email">
        <label for="email">Email</label>
      </div>
      <div class="input-field col s8">
        <input id="address" type="text" class="validate" v-model="address">
        <label for="address">Address</label>
      </div>
      <div class="input-field col s8">
        <input id="credit-card" type="text" class="validate" v-model="creditCard">
        <label for="credit-card">Credit Card Number (16 digits)</label>
      </div>
      <div class="input-field col s8">         
        <div class="btn light-blue" @click="placeOrder">
          <span>place order</span>            
        </div>
      </div>
    </div>
     <p v-if="errors.length" class="error">
    <b>Please correct the following error(s):</b>
    <ul>
      <li v-for="(error, index) in errors" v-bind:key="index">{{ error }}</li>
    </ul>
  </p>
    </form>
  </div>
</template>

<script>
import router from '../router';
import BasketService from '../BasketService';

export default {
  data() {
    return {
      email: "",
      address: "",
      creditCard: "",
      errors: []
    }
  },
  methods: {
    placeOrder(e) {
      if (this.checkForm(e)) {
        BasketService.emptyBasket();
        router.push({ name: "Order"});
      }
    },
    checkForm(e) {
      this.errors = [];

      if (!this.address) {
        this.errors.push("Address required.");
      }
      if (!this.email) {
        this.errors.push('Email required.');
      } else if (!this.validEmail(this.email)) {
        this.errors.push('Valid email required.');
      }
      if (!this.creditCard) {
        this.errors.push('Credit card number required.');
      } else if (!this.validCC(this.creditCard)) {
         this.errors.push('Valid credit card number required.');
      }

      if (!this.errors.length) {
        return true;
      }

      e.preventDefault();
    },
   validEmail(email) {
      const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    },
    validCC(cardNum) {
      const re = /^(?:[0-9]{16})$/;     
      if (cardNum.match(re)) {
        return true;
      }
      return false;          
    }
  }
}
</script>

<style scoped>
p.error {
  border: 1px solid #ff5b5f;
  background-color: #ffc5c1;
  padding: 10px;
  margin-bottom: 15px;
}
/* label focus color */
.input-field input[type=text]:focus + label, .input-field input[type=email]:focus + label {
  color: #03a9f4 ;
}
/* label underline focus color */
.input-field input[type=text]:focus, .input-field input[type=email]:focus {
  border-bottom: 1px solid #03a9f4 ;
  box-shadow: 0 1px 0 0 #03a9f4 ;
}
</style>
