<template>
  <div class="container">
    <div v-if="!items.length">
      <h4 class="grey-text text-darken-2">Your basket is empty</h4> 
    </div>
    <div v-else>
      <div class="btn grey darken-2 right" @click="checkout">
        <span>Checkout</span>            
      </div> 
      <h4 class="grey-text text-darken-2">My Basket</h4>
      <h5 class="grey-text text-darken-2">Total Price: {{   price.toFixed(2) }} EUR</h5>    
      <br>
      <div class="row">
        <div class="input-field col s3">
          <input id="promo-code" type="text" class="validate"   v-model="promoCodes">
          <label for="promo-code">PROMO CODE</label>
          <span class="helper-text" data-error="wrong"  ta-success="right">Insert promo code(s). Use space to  separate.</span>
        </div>
        <div v-if="!promoApplied" class="input-field col s3">         
          <div class="btn-small light-blue" @click="applyPromo">
            <span>apply</span>            
          </div>
        </div>
        <div v-else class="input-field col s3">         
          <div class="btn-small light-blue" @click="removePromo">
            <span>remove</span>            
          </div>
        </div>
      </div>     
      <br>
      <p class="error" v-if="error">{{ error }}</p>
      <div class="items-container">
        <div class="row">
          <div class="item"
           v-for="(item, index) in items"
           v-bind:item="item"
           v-bind:index="index"
           v-bind:key="item._id"
          >      
            <div class="col s12 m8">
              <div class="card horizontal">
                <div class="card-image">
                  <img :src="item.image">
                </div>
                <div class="card-stacked">
                  <div class="card-content">
                    <h5>{{ item.name }}</h5>
                    <br>
                    <div class="orange-text text-darken-2">
                      <p>Item price: {{ item.price }} EUR</p>                 
                      <p>Quantity: {{ item.quantity }}</p>
                    </div>
                    <br>
                    <div class="orange-text text-darken-2">
                      <i class="material-icons" @click="addItem(item) ">add</i>&nbsp;
                      <i class="material-icons" @click="removeItem  (item)">remove</i>
                    </div>               
                  </div>                
                </div>
              </div>                         
            </div>         
          </div>
        </div>     
      </div>
    </div>
  </div>
</template>

<script>
import BasketService from "../BasketService";
import router from "../router";

export default {
  name: "Basket",
  data() {
    return {
      items: [],
      price: 0,
      promoCodes: "",
      promoApplied: false,
      error: "",
      used20PercentOff: false,
      used5PercentOff: false,
      used20EurOff: false
    };
  },   
  methods: {
    getTotalPrice() {  
      let totalPrice = 0;
      this.items.forEach(item => {
        // calculate additional discount for Motion sensors
        if (item.name === "Motion sensor" && item.quantity >= 3) {
          totalPrice += 65 + (item.quantity - 3) * item.price;
        } else if (item.name === "Smoke sensor" && item.quantity >= 2) {
          totalPrice += 35 (item.quantity - 2) * item.price;
        } else {
          totalPrice += item.quantity * item.price;
        }        
      });
      this.price = totalPrice;    
    },
    applyPromo() {
      let promoPrice = this.price;
      const codes = this.promoCodes.split(" ");
     
      codes.forEach(code => {
        switch (code) {
          // 20%OFF code cannot be used in conjuction with other codes
          case "20%OFF":           
            if (!this.used20PercentOff) { 
              promoPrice -= this.price * 0.2;
              this.used20PercentOff = true;
              this.promoApplied = true;              
            }
            break;
          // 5%OFF code can be used in conjuction with other codes
          case "5%OFF":          
            if (!this.used5PercentOff) {
              // decrease price for 5%
              promoPrice -= promoPrice * 0.05;
              // if 20EUROFF code is already applied, check if lower price could be given if 5%OFF code was applied first on the original price. Set whichever price is lower.
              if (!this.used20EurOff) {
                let tempPrice = this.price - this.price * 0.05;
                tempPrice -= 20;
                if (tempPrice < promoPrice) {
                  if (tempPrice < 0) {
                    promoPrice = 0;
                  } else {
                    promoPrice = tempPrice;
                  }                  
                }
              }
              this.used5PercentOff = true;
              this.promoApplied = true;
            }
            break;
          // code can be used in conjuction with other codes
          case "20EUROFF":
            if (!this.used20EurOff) {
              // decrease price for 20 EUR
              promoPrice -= 20;
              // if 5%OFF code is already applied, check if lower price could be given if 20EUROFF code was applied first on the original price. Set whichever price is lower.
              if (this.used5PercentOff) {
                let tempPrice = this.price - 20;
                tempPrice -= tempPrice * 0.05;
                if (tempPrice < promoPrice) {
                  if (tempPrice < 0) {
                    promoPrice = 0;
                  } else {
                    promoPrice = tempPrice;
                  }                  
                }
              }
              this.used20EurOff = true;
              this.promoApplied = true;
            }
            break;
        }
      });  
      this.price = promoPrice;      
    },
    removePromo() {
      this.getTotalPrice();
      this.promoApplied = false;
      this.used20PercentOff = false,
      this.used5PercentOff = false,
      this.used20EurOff = false
      this.promoCodes = "";
    },
    checkout() {
      router.push({ name: "Checkout", params: { price: this.price }});
    },
    async addItem(item) {
      try {
        await BasketService.insertItem({
          _id: item._id,
          name: item.name,
          price: item.price,
          image: item.image
        });
        this.items = await BasketService.getItems();
        this.getTotalPrice();
      } catch (err) {
        this.error = err.message;
      }
    },
    async removeItem(item) {
      try {
        await BasketService.deleteItem(item._id);
        this.items = await BasketService.getItems();
      } catch (err) {
        this.error = err.message;
      }
    }
  },
  async created() {
    try {
      this.items = await BasketService.getItems();
      this.getTotalPrice();
      this.promoCodes = "";
    } catch (err) {
      this.error = err.message;
    }
  }
}
</script>

